import Vue from 'vue'
import App from './App.vue'
import VueMaterial from 'vue-material'
import 'vue-material/dist/vue-material.min.css'
import 'vue-material/dist/theme/default.css'
import * as VueGoogleMaps from "vue2-google-maps";
import VueRouter from 'vue-router'
import Restaurants from "./components/restaurants.vue";
import RestaurantDetail from "./components/restaurantDetail.vue";
import Accueil from "./components/accueil.vue";
import Reserver from "./components/reserver.vue";
import Admin from "./components/admin.vue";
import Maps from "./components/maps.vue";

// configs...
Vue.use(VueMaterial);
Vue.config.productionTip = false;
Vue.use(VueRouter);

Vue.use(VueGoogleMaps, {
	load: {
		key: "AIzaSyAgEYbKNKgH9wALNRhHHGhyI6WN10LNmvg",
		libraries: "places"
	}
  });

// config du router :
const router = new VueRouter({
	routes:[
		{ 
			path: '/', 
			component: Accueil,
			props: {
				msg: "Rest-Menu, le site qui vous presente les restaurants dans le monde !"
			} 
		},

		{ 
			path: '/restaurants', 
			component: Restaurants 
		},

		{ 
			path: '/restaurant/details/:id', 
			component: RestaurantDetail 
		},

		{
			path: '/restaurant/reserver/:id',
			component: Reserver
		},

		{
			path: '/restaurants/admin/:id',
			component: Admin
		},

		{
			path: '/restuarants/maps/id:',
			component: Maps
		}
	],
	mode:'history'
});

new Vue({
	router,
	render: h => h(App), // si router pas de rendu de composant
}).$mount('#app')
